import React from "react";
import { Grid, Typography, Box } from "@material-ui/core";
import ScrollAnimation from 'react-animate-on-scroll';

import Parallax from "./Parallax";
import image from "../images/technicals-bg.jpg";

const masteries = [
    [
        { icon: "devicon-bitbucket-plain", level: "80" },
        { icon: "devicon-codeigniter-plain", level: "85" },
        { icon: "devicon-debian-plain", level: "60" },
        { icon: "devicon-docker-plain", level: "65" },
        { icon: "devicon-express-original-wordmark", level: "70" },
        { icon: "devicon-git-plain", level: "75" },
        { icon: "devicon-github-plain", level: "60" },
        { icon: "devicon-gitlab-plain", level: "60" },
        { icon: "devicon-illustrator-plain", level: "40" },
        { icon: "devicon-javascript-plain", level: "80" },
        { icon: "devicon-mongodb-plain", level: "60" }
    ],
    [
        { icon: "devicon-mysql-plain", level: "80" },
        { icon: "devicon-nodejs-plain", level: "70" },
        { icon: "devicon-php-plain", level: "70" },
        { icon: "devicon-postgresql-plain", level: "60" },
        { icon: "devicon-react-original", level: "60" },
        { icon: "devicon-sequelize-plain", level: "70" },
        { icon: "devicon-typescript-plain", level: "50" },
        { icon: "devicon-ubuntu-plain", level: "75" },
        { icon: "devicon-vagrant-plain", level: "65" },
        { icon: "devicon-visualstudio-plain", level: "60" },
        { icon: "devicon-vuejs-plain", level: "65" }
    ]
]

export default function Technicals() {
    const generateDelay = () => {
        return Math.floor(Math.random() * 2000) + 500
    }

    return <Parallax bg={image}>
        <Grid container alignItems="center" alignContent="center">
            <Grid key={`mastery-icon0-grid-0`} container item alignContent="center" justify="center" style={{margin: 30}}>
                <Typography variant="h2" color="primary" align="center">
                    <Box fontWeight="fontWeightBold">
                        MASTERY
                    </Box>
                </Typography>
            </Grid>
            {masteries.map((rec, index) => {
                return <Grid key={`mastery-grid-${index+1}`} container item sm={12} md={6} lg={6} alignItems="center" alignContent="center">
                    {rec.map((mastery, index) => {
                        return <Grid key={`mastery-icon0-grid-${index}`} container item sm={4} md={3} lg={3} alignContent="center" justify="center">
                            <ScrollAnimation animateIn="animate__bounceIn" delay={generateDelay()} duration={2} animateOnce >
                                <Typography variant="h1" color="secondary" align="center">
                                    <i className={mastery.icon} />
                                </Typography>
                            </ScrollAnimation>
                        </Grid>
                    })}
                </Grid>
            })}
        </Grid>
    </Parallax>
}