import React from "react";
import { Grid, Typography, Box } from "@material-ui/core";
import ScrollAnimation from 'react-animate-on-scroll';

class Bio extends React.Component {
    render() {
        return <Grid container alignItems="center" alignContent="center" justify="center">
            <Grid container item xs={6} md={6} lg={6} direction="column" alignContent="center" justify="center" style={{margin: "3%"}}>
                <ScrollAnimation animateIn="animate__fadeInUp" animateOnce >
                    <Typography variant="h1" component="div" color="primary" align="center">
                        <Box fontWeight="fontWeightBold">
                            Hi! I'm Jeff
                        </Box>
                    </Typography>
                </ScrollAnimation>
                <ScrollAnimation animateIn="animate__fadeInUp" animateOnce offset={300} >
                    <Typography variant="h6" component="div" color="secondary" align="center">
                        A risk-taker, challenge-maniac. Eager to break its limiter.
                        Experienced and able to integrate solutions into enterprise-level systems.
                    </Typography>
                </ScrollAnimation>
            </Grid>
        </Grid>
    }
}

export default Bio